<template>
  <div class="write-contact-us wrapper">
    <div class="content">
      <div class="header-title">
        <div class="title">
          联系我们
        </div>
        <div class="icon" @click="closeBtn">
          <img src="../assets/img/contactUs/btn_X_1.png" alt="">
        </div>
      </div>
      <div class="main">
        <div class="main-text">
          如您在华龙讯达产品、解决方案和服务方面存有疑问，敬请留下您的联系方式，我们会安排专人与您取得联系，谢谢！
        </div>
        <div class="main-title">
          个人信息
        </div>
        <!-- 上传图片 -->
        <div v-if="!imgUrl" class="img-box">
          <div class="img-icon">
            <img src="../assets/img/contactUs/btn_update_pic_busicard.png" alt="">
          </div>
          <div class="img-font">
            点击上传名片照片
          </div>
          <input type="file" class="input-file" @change="changeImage($event)" accept="image/gif,image/jpeg,image/jpg,image/png">
        </div>
        <div v-else class="img-box">
          <img :src="imgUrl" alt="">
          <input type="file" class="input-file" @change="changeImage($event)" accept="image/gif,image/jpeg,image/jpg,image/png">
        </div>
        <!-- 采购计划 -->
        <div class="main-title pt60">
          请问贵公司是否有新项目启动/扩建/升级/改造或产品采购计划？
        </div>
        <div class="purchase">
          <div class="radio-item">
            <radio :radio="purchase" value="1" name="有计划，1年以内" @radioBtn="purchaseBtn"></radio>
          </div>
          <div class="radio-item">
            <radio :radio="purchase" value="2" name="有计划，1-3年" @radioBtn="purchaseBtn"></radio>
          </div>
          <div class="radio-item">
            <radio :radio="purchase" value="3" name="有计划，3年以后" @radioBtn="purchaseBtn"></radio>
          </div>
          <div class="radio-item">
            <radio :radio="purchase" value="4" name="暂无相关计划" @radioBtn="purchaseBtn"></radio>
          </div>
        </div>
        <!-- 联系方法 -->
        <div class="main-title pt25">
          您希望华龙讯达销售人员采取下列哪一种方式与您取得进一步联系？
        </div>
        <div class="contact">
          <div class="radio-item">
            <radio :radio="contact" value="1" name="预约拜访" @radioBtn="contactBtn"></radio>
          </div>
          <div class="radio-item">
            <radio :radio="contact" value="2" name="电话沟通" @radioBtn="contactBtn"></radio>
          </div>
          <div class="radio-item">
            <radio :radio="contact" value="3" name="暂无需要" @radioBtn="contactBtn"></radio>
          </div>
        </div>

        <div class="submit" @click="submitBtn">
          提交
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Radio from './radio.vue'
export default {
  data() {
    return {
      imgUrl: '',  //图片本地路径
      file: '',    //上传的图片 
      purchase: '1',//采购计划  下标
      contact: '1', //联系方法  下标
      plan:'有计划，1年以内',   //采购计划  名称
      intention:'预约拜访',   //联系方法  名称
    }
  },
  methods: {
    purchaseBtn(obj) {   //采购计划  选择
      this.purchase = obj.val;
      this.plan = obj.name;
    },
    contactBtn(obj) {   //联系方法  选择
      this.contact = obj.val;
      this.intention = obj.name;
    },
    closeBtn() {    //关闭按钮
      this.$emit('closeBtn');
    },
    submitBtn() {   //提交按钮
      if(this.file === ''){
        return;
      }
      this.uploadPictures();
    },
    async uploadPictures(){
      try{
        const formData = new FormData();
        formData.append('file', this.file);

        let imgRes = await this.$api.files.uploadImg({  //上传图片
          file: formData
        })

        console.log(imgRes);
        if(imgRes.statusCode !== 75200 && imgRes.statusCode !== 75600){ //判断图片是否上传成功
          return
        }
        let openid = this.$zero.getOpenid();
        let contactRes = await this.$api.contactUs.postContact({  //提交用户联系内容
          plan: this.plan,
          intention:this.intention,
          callCard: imgRes.server_name,
          openid
        })

        console.log(contactRes);
        if(contactRes.statusCode === 75200){
          this.$emit('closeBtn');
        }
        
        
      } catch (err) {
        console.log(err);
      }
    },
    changeImage(e) {  //获取图片
      // console.log(event);
      let file = e.target.files[0];
      if (file) {
        this.file = file
        this.imgUrl = this.$zero.fileToBlobUrl(this.file);
      }
    }
  },
  components: {
    Radio
  }
}
</script>

<style scoped>
.write-contact-us {
  padding: 0 0.2rem;
  display: flex;
  flex-direction: column-reverse;
}
.write-contact-us .content {
  /* position: absolute; */
  width: 100%;
  /* bottom: 0; */
  /* left: 0; */
  background: url("../assets/img/contactUs/bg_popup.png") no-repeat;
  background-size: 100% 100%;
}
.write-contact-us .header-title {
  padding: 0.3rem 0.2rem 0.3rem 0.4rem;
  border-bottom: 1px solid #006baf;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.write-contact-us .title {
  font-size: 0.4rem;
  color: #fff;
  font-weight: bold;
}
.write-contact-us .icon {
  width: 0.3rem;
  height: 0.31rem;
}
.write-contact-us .icon > img {
  width: 100%;
  height: 100%;
}
.write-contact-us .main {
  padding: 0.4rem 0.4rem 0.45rem;
}
.write-contact-us .main-text {
  font-size: 0.24rem;
  color: #fff;
}
.main-title {
  font-size: 0.28rem;
  padding-top: 0.35rem;
  padding-bottom: 0.25rem;
  color: #00edff;
}
.img-box {
  width: 100%;
  height: 3.6rem;
  box-sizing: border-box;
  border: 1px solid #1a5169;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.img-box > img {
  width: 100%;
  height: 100%;
}
.img-box .input-file {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 3.6rem;
  opacity: 0;
}
.img-icon {
  width: 1.2rem;
  height: 1.2rem;
}
.img-icon > img {
  width: 100%;
  height: 100%;
}
.img-font {
  font-size: 0.24rem;
  color: #8292a2;
  padding-top: 0.5rem;
}
.pt60 {
  padding-top: 0.6rem;
}
.pt25 {
  padding-top: 0.25rem;
}
.purchase {
  display: flex;
  flex-wrap: wrap;
}
.purchase .radio-item {
  margin-right: 1.2rem;
  padding-bottom: 0.25rem;
}
.purchase .radio-item:nth-child(2n) {
  margin-right: 0;
}
.contact {
  display: flex;
  /* flex-wrap: wrap; */
  padding-bottom: 0.5rem;
}
.contact .radio-item {
  margin-right: 1rem;
}
.contact .radio-item:last-child {
  margin-right: 0;
}
.submit {
  width: 2.84rem;
  height: 0.84rem;
  margin: 0 auto;
  color: #fff;
  font-size: 0.28rem;
  text-align: center;
  line-height: 0.84rem;
  background: url("../assets/img/btn_3.png") no-repeat;
  background-size: 100% 100%;
}

.radio {
  /* color: #606266; */
  font-weight: 500;
  height: 0.32rem;
  line-height: 0.32rem;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  outline: none;
  /* font-size: 14px; */
  /* margin-right: 30px; */
}

.rabio-input {
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  display: inline-block;

  position: relative;
  vertical-align: middle;
}
.radio-inner {
  /* border: 1px solid #dcdfe6; */
  border-radius: 100%;
  width: 0.3rem;
  height: 0.3rem;
  /* background-color: #fff; */
  position: relative;
  cursor: pointer;
  display: inline-block;
  box-sizing: border-box;
  background: url("../assets/img/radio/checkbox_0.png") no-repeat;
  background-size: 100% 100%;
}
.radio-original {
  opacity: 0;
  outline: none;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
}
.radio-label {
  font-size: 0.26rem;
  color: #fff;
  /* padding-left: 10px; */
}
.rabio-input.is-checked .radio-inner::after {
  content: "";
  width: 0.3rem;
  height: 0.3rem;
  border-radius: 100%;
  background: url("../assets/img/radio/checkbox_1.png") no-repeat;
  background-size: 100% 100%;
  /* background-color: #fff; */

  position: absolute;
  left: 0;
  top: 0;

  transition: transform 0.15s ease-in;
}
</style>