<template>
  <div>
    <label class="radio is-checked" @click="radioBtn">
      <span class="rabio-input" :class="{'is-checked': radio == value}">
        <span class="radio-inner"></span>
        <!-- <input type="radio" aria-hidden="true" class="radio-original" :value="value"> -->
      </span>
      <span class="radio-label">
        {{name}}
      </span>
    </label>
  </div>
</template>

<script>
export default {
  props:{
    value:{
      type: String
    },
    name:{
      type: String
    },
    radio: {
      type: String
    }
  },
  methods: {
    radioBtn(){
      this.$emit('radioBtn', {val: this.value, name: this.name});
    }
  }

}
</script>

<style scoped>
.radio{
    /* color: #606266; */
    font-weight: 500;
    height: .32rem;
    line-height: .32rem;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    outline: none;
    /* font-size: 14px; */
    /* margin-right: 30px; */
}

.rabio-input{
  white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    
    position: relative;
    vertical-align: middle;
}
.radio-inner{
    /* border: 1px solid #dcdfe6; */
    border-radius: 100%;
    width: .3rem;
    height: .3rem;
    /* background-color: #fff; */
    position: relative;
    cursor: pointer;
    display: inline-block;
    box-sizing: border-box;
    background: url("../assets/img/radio/checkbox_0.png") no-repeat;
    background-size: cover;
}
.radio-original{
    opacity: 0;
    outline: none;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
}
.radio-label{
  font-size: .26rem;
  color: #fff;
  /* padding-left: 10px; */
}
.rabio-input.is-checked .radio-inner::after {
    content: "";
    width: .3rem;
    height: .3rem;
    border-radius: 100%;
    background: url("../assets/img/radio/checkbox_1.png") no-repeat;
    background-size: cover;
    /* background-color: #fff; */
    
    position: absolute;
    left: 0;
    top: 0;

    transition: transform .15s ease-in;

}
</style>