<template>
  <div class="contact-us">
    <div class="header">
      <public-head title="联系我们"></public-head>
    </div>
    <div class="main">
      <div class="title-box">
        <div class="left-title">
          <div class="main-title">
            {{exhibitionInfo.exhibitionBooth}}
          </div>
          <div class="vice-title">
            {{exhibitionInfo.name}}
          </div>
          <div class="title-text">
            <span class="time">
              {{exhibitionInfo.time}}
            </span>
            <span class="place">
              {{exhibitionInfo.addr}}
            </span>
          </div>
        </div>
        <div class="right-logo" @click="isStaffOnly = true">
          <img src="../../assets/img/logo.png" alt="">
        </div>
      </div>

      <div class="content">
        <div class="contacts">
          <div class="title">
            联系人
          </div>
          <div class="contacts-list">
            <div class="contacts-item" v-for="(item,index) in companyDetail.people" :key="index">
              <span class="contacts-name">{{item.name}}</span>
              <span class="contacts-phone">{{item.phone}}</span>
            </div>
            <!-- <div class="contacts-item">
              <span class="contacts-name">聂先生</span>
              <span class="contacts-phone">13800138000</span>
            </div> -->
          </div>
        </div>
        <div class="company-info">
          <div class="title">
            公司信息
          </div>
          <div class="company-info-list">
            <div class="company-info-item">
              <div class="company-info-icon">
                <img src="../../assets/img/contactUs/ico_firm_location.png" alt="">
              </div>
              <div class="company-info-font">
                {{companyDetail.addr}}
              </div>
            </div>
            <div class="company-info-item">
              <div class="company-info-icon">
                <img src="../../assets/img/contactUs/ico_firm_postnumber.png" alt="">
              </div>
              <div class="company-info-font">
                {{companyDetail.short}}
              </div>
            </div>
            <div class="company-info-item">
              <div class="company-info-icon">
                <img src="../../assets/img/contactUs/ico_firm_phone.png" alt="">
              </div>
              <div class="company-info-font">
                {{companyDetail.phone}}
              </div>
            </div>
            <div class="company-info-item">
              <div class="company-info-icon">
                <img src="../../assets/img/contactUs/ico_firm_fax.png" alt="">
              </div>
              <div class="company-info-font">
                {{companyDetail.fax}}
              </div>
            </div>

            <div class="company-tips-position">
              <div class="qrcode">
                <img src="../../assets/img/contactUs/pic_QRcode.png" alt="">
              </div>
              <div class="line">
                <img src="../../assets/img/contactUs/line_officialaccounts.png" alt="">
              </div>
              <div class="company-tips">
                更多资讯敬请关注华龙讯达公众号
              </div>
            </div>
          </div>

        </div>
        <div class="contact-us-btn" @click="contact">
          联系我们
        </div>
      </div>
    </div>

    <div v-show="isStaffOnly" class="staff-only wrapper" @click="isStaffOnly = false">
      <div class="staff" @click.stop="()=>{}">
        <div class="staff-title">
          员工通道
        </div>
        <div class="staff-content">
          <div class="staff-tips">
            请输入密码
          </div>
          <div>
            <input class="staff-input" type="password" v-model="password">
          </div>
        </div>
        <div class="contact-us-btn" @click="staffOnly">
          确定
        </div>
      </div>
    </div>

    <!-- 填写联系我们的资料 -->
    <write-contact-us v-show="isContact" @closeBtn="closeBtn"></write-contact-us>
  </div>
</template>

<script>
import PublicHead from '../../components/publicHead.vue'
import WriteContactUs from '../../components/writeContactUs.vue'
export default {
  data() {
    return {
      isContact: false,   //联系我们
      isStaffOnly: false,   //员工通道
      password: '123456789',
      exhibitionInfo: '',   //展会信息
      companyDetail: '',   //公司信息

    }
  },
  methods: {
    contact() {   //联系我们按钮
      this.isContact = true;
    },
    closeBtn() {  //关闭联系我们
      this.isContact = false;
    },
    staffOnly() {  //员工通道
      console.log(123);
      this.$router.push('/staffWorkbench')
    },
    getExhibitionInfo(){  //获取展会信息
      this.$api.contactUs.exhibitionInfo({}).then(res=>{
        console.log('展会信息',res);
        if(res.statusCode === 75200 ){
          this.exhibitionInfo = res.result;
        }
      }).catch(err=>{
        console.log(err);
      })
    },
    getCompanyDetail(){    //获取公司信息
      this.$api.contactUs.companyDetail().then(res=>{
        console.log(res);
        if(res.statusCode === 75200 ){
          this.companyDetail = res.result;
        }
        
      }).catch(err=>{
        console.log(err);
        
      })
    }
  },
  created(){
    this.getExhibitionInfo();
    this.getCompanyDetail();
  },
  components: {
    PublicHead,
    WriteContactUs
  }
}
</script>

